/*=============== BREAKPOINTS ===============*/
/* For small devices */
@media screen and (max-width: 340px) {
  .container {
    margin-inline: 1rem;
  }

  .section__title {
    font-size: var(--h2-font-size);
  }

  .home__title {
    font-size: 3rem;
  }

  .home__buttons {
    flex-direction: column;
    row-gap: 1rem;
  }

  .home__circle {
    width: 250px;
    height: 250px;
  }

  .home__subcircle {
    width: 200px;
    height: 200px;
  }

  .home__images .home__img {
    width: 200px;
  }

  .footer__content {
    grid-template-columns: 1fr;
    justify-items: center;
    row-gap: 1.5rem;
  }

  .footer__copy {
    grid-column: 1;
  }

}

/* For medium devices */
@media screen and (min-width: 540px) {

  .home__container,
  .favorites__container,
  .care__container {
    grid-template-columns: 370px;
    justify-content: center;
  }

  .favorites__swiper {
    max-width: 600px;
    overflow-x: clip;
    justify-self: center;
  }

  .contact {
    display: grid;
    grid-template-columns: 450px;
    justify-content: center;
  }
}

@media screen and (min-width: 768px) {
  .contact {
    grid-template-columns: 550px;
  }

  .footer__content {
    grid-template-columns: repeat(3, max-content);
    justify-content: space-between;
    align-items: center;
  }

  .footer__copy {
    grid-column:
      initial;
  }

  .footer__link:nth-child(2) {
    order: 1;
  }
}

/* For large devices */
@media screen and (min-width:1150px) {
  .container {
    margin-inline: auto;
  }

  .section {
    padding-block: 7rem 2rem;
  }

  .section__title {
    margin-bottom: 2rem;
  }

  nav {
    height: calc(var(--header-height) + 2rem);
  }

  .nav__toggle,
  .nav__close {
    display: none;
  }

  .nav__link {
    font-size: var(--normal-font-size);
  }

  .nav__menu {
    display: flex;
    column-gap: 3rem;
    align-items: center;
  }

  .nav__list {
    flex-direction: row;
    column-gap: 4rem;
  }

  .home__container {
    grid-template-columns: 420px 585px;
    align-items: center;
    column-gap: 6rem;
    padding-top: 5rem;
  }

  .home__data {
    text-align: initial;
  }

  .home__title {
    margin-bottom: 1.5rem;
  }

  .home__description {
    margin-bottom: 4rem;
  }

  .home__buttons {
    justify-content: initial;
    column-gap: 1.5rem;
  }

  .home__meat {
    width: 80px;
    top: 13rem;
  }

  .home__circle {
    width: 550px;
    height: 550px;
  }

  .home__subcircle {
    width: 430px;
    height: 430px;
  }

  .home__images .home__img {
    width: 400px;
  }

  .home__images img {
    width: 95px;
  }

  .favorites__container {
    grid-template-columns: 520px;
    row-gap: 4rem;
  }

  ul {
    list-style-type: none; /* Remove bullets or markers */
  }

  a {
    text-decoration: none;}

  .favorites__cheese-1,
  .favorites__cheese-2 {
    width: 80px;
  }

  .favorites__cheese-1 {
    right: -18rem;
  }

  .favorites__cheese-2 {
    left: -18rem;
  }

  .favorites__swiper {
    max-width: 1000px;
  }

  .favorites__article,
  .favorites__img {
    width: 420px;
  }

  .favorites__leaf-1,
  .favorites__leaf-2 {
    width: 110px;
  }

  .care__container {
    grid-template-columns: 500px 410px;
    column-gap: 6rem;
    align-items: center;
    padding-block: 4rem;
  }

  .care__img {
    width: 550px;
    order: -1;
  }

  .care__item {
    column-gap:
      1rem;
  }

  .care__item i {
    font-size: 2rem;
  }

  .banner {
    padding-top: 4rem;
  }

  .banner__list {
    column-gap: 6.5rem;
  }

  .banner__item {
    column-gap: 1rem;
  }

  .banner__item i {
    font-size: 4.5rem;
  }

  .banner__item span {
    font-size: var(--h1-font-size);
  }

  .contact {
    display: block;
  }

  .contact__container {
    border-radius: 4rem;
  }

  .contact__content {
    grid-template: 330px / 360px 320px;
    justify-content: center;
    align-items: flex-start;
    column-gap: 10rem;
    padding-top: 2rem;
  }

  .contact__data {
    text-align: initial;
    grid-template: max-content / repeat(2, max-content);
    gap: 3rem 2rem;
  }

  .contact__title {
    margin-bottom: .75rem;
  }

  .contact__social {
    justify-content: initial;
  }

  .contact__img {
    width: 320px;
    transform: translateY(-6rem);
  }

  .contact__shrimp,
  .contact__crab,
  .contact__meat {
    width: 80px;
  }

  .contact__shrimp {
    left: 0;
    right: 0;
    margin: 0 auto;
    top: 20rem;
  }

  .contact__crab {
    top: 5rem;
    left: 4rem;
  }

  .contact__meat {
    right: initial;
    left: 12rem;
    bottom: -1rem;
  }

  .footer {
    padding-block: 5rem 3rem;
  }

  .footer__container {
    row-gap:
      3rem;
  }

}

